export const generateName = (): string => {
  let name = "";

  if (Math.random() < 0.1) {
    let n = Math.floor(Math.random() * fullNames.length);
    name = fullNames[n];
  } else {
    let f = Math.floor(Math.random() * firstNames.length);
    let l = Math.floor(Math.random() * lastNames.length);
    name = `${firstNames[f]} ${lastNames[l]}`;
  }
  return name;
};
